import React, { useEffect, useState } from "react";
import { Pagination, message, Spin, Tag } from "antd";

import { getLawyerList, getStatusUpdate } from "../Common/Api-service";

import moment from "moment";

import Table from "react-bootstrap/Table";
// import Spinner from 'react-bootstrap/Spinner';
import { Card, Row, Col, CardTitle, CardBody } from "reactstrap";

import approved from "../assets/images/Action/check.png";
import cancel from "../assets/images/Action/cancel.png";

// import { getLawyerRequest } from '../Common/Api-service';

const LawyerMaster = () => {
  const [listData, setListData] = useState([]);
  const [showSpinner, setSpinner] = useState(true);
  const [totalCount, setTotalCount] = useState();
  const [currentPage, setCurrentPage] = useState(1);
  const [lawyerlistData, setLawyerData] = useState([]);
  const [messageApi, contextHolder] = message.useMessage();
  const [offSet, setOffSet] = useState(false);

  // const getAllLawyerRequest = () => {

  //     getLawyerRequest(currentPage).then((res) => {
  //         // console.log(res.data.Data);
  //          console.log(res)
  //         const LawyerRequestData = res.data.data;
  //         setListData(LawyerRequestData);
  //         setSpinner(false);
  //         setTotalCount(res.data.totalCount);
  //     }).catch((err) => {
  //         console.log(err)
  //         setSpinner(false);
  //     })

  // }

  // useEffect(() => {

  //     getAllLawyerRequest()

  // }, [currentPage])

  const isApproved = (lawyerId, ustatus) => {
    const payload = {
      _id: lawyerId,
      isApproved: ustatus,
    };

    console.log("payload", payload);

    getStatusUpdate(payload).then((res, i) => {
      console.log("res:", res.data);
      if (res.data.status === true) {
        setOffSet(!offSet);
        messageApi.open({
          type: "success",
          content: `${res.data.message}`,
        });
      } else {
        messageApi.open({
          type: "error",
          content: `${res.data.message}`,
        });
      }
    });
  };

  const getAllLawyersList = () => {
    getLawyerList(currentPage)
      .then((res) => {
        console.log(res);
        const lawyerlist = res.data.data;
        setLawyerData(lawyerlist);
        setSpinner(false);
        setTotalCount(res.data.totalcount);
      })
      .catch((err) => {
        console.log(err);
        setSpinner(false);
      });
  };

  useEffect(() => {
    getAllLawyersList();
  }, [currentPage, offSet]);

  const handleChangePage = (page) => {
    console.log("hit");
    setCurrentPage(page);
  };

  return (
    <>
      {contextHolder}
      <Row>
        <Col>
          {/* --------------------------------------------------------------------------------*/}
          {/* Card-1*/}
          {/* --------------------------------------------------------------------------------*/}
          <Card className="mt-5">
            <CardTitle tag="h6" className="border-bottom p-3 mb-0">
              <i className="bi bi-bell me-2"> </i>
              Lawyer Master
            </CardTitle>
            {/* {showSpinner && <Spinner style={{ margin: "5px auto" }} animation="border" variant="primary" />} */}

            <CardBody>
              <Spin id="spin" spinning={showSpinner}>
                <Table striped bordered hover responsive>
                  <thead>
                    <tr>
                      <th className="font-weight-bold">Sr.No</th>
                      <th className="font-weight-bold">Full Name</th>
                      <th className="font-weight-bold">Speciality</th>
                      <th className="font-weight-bold">City</th>
                      <th className="font-weight-bold">Email</th>
                      <th className="font-weight-bold">Phone No.</th>
                      <th className="font-weight-bold">createdOn</th>
                      <th className="font-weight-bold">updatedOn</th>
                      <th className="font-weight-bold">Status</th>
                      <th className="font-weight-bold">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {lawyerlistData.length >= 1 ? (
                      lawyerlistData.map((data, i = 0) => {
                        return (
                          <tr key={data._id}>
                            <td>{i + 1}</td>
                            <td>{data.name}</td>
                          <td>{ data.specialization.map((specialization, index) =>(
                            <Tag color="purple" key={index} style={{marginTop:"0.3rem"}}>{specialization.name}</Tag>
                            ))
                            }</td>
                            <td>{data.city}</td>
                            <td>{data.email}</td>
                            <td>{data.phoneNo}</td>
                            <td>
                              {moment(data.createdOn).format("M/DD/YYYY")}
                            </td>
                            <td>
                              {moment(data.updatedOn).format("M/DD/YYYY")}
                            </td>

                            <td>
                              <Tag
                                color="green"
                                hidden={
                                  data.isApproved == "rejected" ||
                                  data.isApproved == "pending"
                                    ? true
                                    : false
                                }
                              >
                                {data.isApproved}
                              </Tag>
                              <Tag
                                color="red"
                                hidden={
                                  data.isApproved == "approved" ||
                                  data.isApproved == "pending"
                                    ? true
                                    : false
                                }
                              >
                                {data.isApproved}
                              </Tag>
                              <Tag
                                color="cyan"
                                hidden={
                                  data.isApproved == "approved" ||
                                  data.isApproved == "rejected"
                                    ? true
                                    : false
                                }
                              >
                                {data.isApproved}
                              </Tag>
                            </td>

                            <td style={{width:"80px"}}>
                              <img
                                src={approved}
                                width="25px"
                                title="approved"
                                id={data._id}
                                onClick={() => {
                                  isApproved(data._id, "approved");
                                }}
                                hidden={
                                  data.isApproved == "pending" ? false : true
                                }
                              />

                              <img
                                src={cancel}
                                width="25px"
                                style={{ marginLeft: "2px" }}
                                title="rejected"
                                id={data._id}
                                onClick={() => {
                                  isApproved(data._id, "rejected");
                                }}
                                hidden={
                                  data.isApproved == "approved" ||
                                  data.isApproved == "pending"
                                    ? false
                                    : true
                                }
                              />
                            </td>
                          </tr>
                        );
                      })
                    ) : (
                      <tr>
                        <td colSpan={14} style={{ textAlign: "center" }}>
                          <strong>No Users available</strong>
                        </td>
                      </tr>
                    )}
                  </tbody>
                </Table>
                {console.log("length", totalCount)}
                <Pagination
                  onChange={(pg) => handleChangePage(pg)}
                  defaultCurrent={currentPage}
                  total={totalCount}
                />
              </Spin>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </>
  );
};

export default LawyerMaster;
