import { api } from "./service-config";

export function getArticleList(){
    return api().get('dev_formulaw/artical');
}


export function getLawyerRequest(Page){
    return api().get(`dev_formulaw/user/getusers/?page=${Page}&limit=10`);
}


export function formulawAdminLogin(payLoad){

    console.log("payload :", payLoad);


    return api().post('dev_formulaw/admin/login', JSON.stringify(payLoad));

}

export function getLawyerList(currentPage){
    return api().get(`dev_formulaw/lawyer/getLawyers/?page=${currentPage}&limit=10`)
}

export function getStatusUpdate(payload){
    return api().put(`/dev_formulaw/lawyer/updateStatus`,JSON.stringify(payload))
}



